    cursor: pointer;
  }
  .rc-tree .rc-tree-treenode span.rc-tree-switcher.rc-tree-switcher_open {
    @include svgBackground(
        "data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3Csvg width='14px' height='14px' viewBox='0 0 14 14' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns='http://www.w3.org/2000/svg'%3E%3Cdesc%3ECreated with Lunacy%3C/desc%3E%3Cpath d='M0 7C0 10.864 3.136 14 7 14C10.864 14 14 10.864 14 7C14 3.136 10.864 0 7 0C3.136 0 0 3.136 0 7ZM7 1C3.6925 1 1 3.6925 1 7C1 10.3075 3.6925 13 7 13C10.3075 13 13 10.3075 13 7C13 3.6925 10.3075 1 7 1ZM3.5 7.58333L3.5 6.41667L10.5 6.41667L10.5 7.58333L3.5 7.58333Z' id='Button' fill='%230891CD' fill-rule='evenodd' stroke='none' /%3E%3C/svg%3E",
        14px,
        14px
    );
    display: inline-block;
    margin-right: 10px;
    line-height: 16px;
    vertical-align: middle;
    border: 0 none;
    outline: none;
    cursor: pointer;
  }
  .rc-tree .rc-tree-treenode span.rc-tree-switcher.rc-tree-switcher_close {
    @include svgBackground(
        "data:image/svg+xml,%3C%3Fxml version='1.0' encoding='utf-8'%3F%3E%3Csvg width='14px' height='14px' viewBox='0 0 14 14' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns='http://www.w3.org/2000/svg'%3E%3Cdesc%3ECreated with Lunacy%3C/desc%3E%3Cpath d='M7 0C3.136 0 0 3.136 0 7C0 10.864 3.136 14 7 14C10.864 14 14 10.864 14 7C14 3.136 10.864 0 7 0ZM1 7C1 10.3075 3.6925 13 7 13C10.3075 13 13 10.3075 13 7C13 3.6925 10.3075 1 7 1C3.6925 1 1 3.6925 1 7ZM6.41667 6.41667L6.41667 3.5L7.58333 3.5L7.58333 6.41667L10.5 6.41667L10.5 7.58333L7.58333 7.58333L7.58333 10.5L6.41667 10.5L6.41667 7.58333L3.5 7.58333L3.5 6.41667L6.41667 6.41667Z' id='Button' fill='%230891CD' fill-rule='evenodd' stroke='none' /%3E%3C/svg%3E",
        14px,
        14px
    );
    display: inline-block;
    margin-right: 10px;
    line-height: 16px;
    vertical-align: middle;
    border: 0 none;
    outline: none;
    cursor: pointer;
  }

  .rc-tree .rc-tree-treenode span.rc-tree-iconEle {
      display: none;
  }